<template>
    <div class="base-box div-pemission">
        <div class="debug-box">
            <div class="debug-box">
                <div class="debug-line">
                    <span class="debug-text text-underline">{{ $t('dbgbase.debug') }}</span>
                </div>
                <div class="first-start-box">
                    <div class="left-box">
                        <IconTextButton :module="module" class="start-one" :enabled="!acRun" @click="devStart"
                            :isUseDev="!acRun" :text="$t('dbgbase.startup')" :iconName="startOneIcon"
                            :textColor="startOneTextColor" iconSize="5.75rem" interval="-1rem" fontSize="1.375rem">
                        </IconTextButton>
                        <IconTextButton :module="module" class="stop-one" :enabled="!acRun" @click="devStop"
                            :isUseDev="!acRun" :text="$t('dbgbase.stop')" :iconName="stopOneIcon"
                            :textColor="stopOneTextColor" iconSize="5.75rem" interval="-1rem" fontSize="1.375rem">
                        </IconTextButton>
                    </div>
                    <div class="right-box">
                        <FormItem :label="$t('dbgbase.rotatVelocity')" labelWidth="5rem" outClass="form-item"
                            leftDis="5.375vw">
                            <InputVal :module="module" :sensorValue="devParams.jog_speed" width="13.5rem"
                                :editable="allowSetParam" :unit="getUnit('angle_speed')"
                                @updateData="setParams('jog_speed', ($event.sensorValue))" uname="angle_speed"></InputVal>
                        </FormItem>
                        <FormItem :label="$t('dbgbase.torqueGard')" labelWidth="5rem" outClass="form-item"
                            leftDis="5.375vw">
                            <InputVal :maxNum="this.$store.state.g_status.sensor_max * 1.25" :module="module"
                                :sensorValue="devParams.jog_maxtor" width="13.5rem" :editable="allowSetParam"
                                :unit="getUnit('torque')" @updateData="setParams('jog_maxtor', ($event.sensorValue))"
                                uname="torque"></InputVal>
                        </FormItem>
                        <FormItem :label="$t('dbgbase.directRotation')" labelWidth="5rem" outClass="form-item"
                            leftDis="5.375vw">
                            <SvgSelect :module="module" :width="13.5" :realVal="devParams.jog_drc" :showImages="true"
                                :isAllowChng="allowSetParam" :options="direction" @valChng="setParams('jog_drc', ($event))">
                            </SvgSelect>
                        </FormItem>
                    </div>
                </div>
                <div class="second-start-box">
                    <IconTextButton :module="module" class="start-two" @click="run" :isUseDev="!devStatus"
                        :iconName="startTwoIcon" :textColor="startTwoTextColor" :text="$t('dbgbase.processStartUp')"
                        iconSize="3.125rem" fontSize="1.375rem"></IconTextButton>
                    <IconTextButton :module="module" class="stop-two" @click="stop" :isUseDev="!devStatus"
                        :iconName="stopTwoIcon" :textColor="stopTwoTextColor" :text="$t('dbgbase.processStop')"
                        iconSize="3.125rem" fontSize="1.375rem"></IconTextButton>
                    <FormItem :label="$t('dbgbase.cycleRun')" labelWidth="5rem" :zIndex="2" outClass="repeat-run" leftDis="5.375vw"
                        class="cyc-box">
                        <LeetxSwitch :module="module" :value="keeprun" :activeVal="true" :staticVal="false" :height="2"
                            class="repeat-btn" @sentVal="runMore($event)"></LeetxSwitch>
                    </FormItem>
                </div>
            </div>

        </div>
        <div class="horizontal-line"></div>
        <div class="base-box-two" v-if="false">
            <div class="machinery-line">
                <span class="text text-underline">{{ $t('dbgbase.motor') }}</span>
                <LeetxButton :module="module" class="operate-btn" textColor="#fff" borderRadius="4px" bgcColor="#00c155"
                    width="17.25vw" height="3.9063vh" fontSize="1.5rem" :text="$t('dbgbase.send')"></LeetxButton>
            </div>
            <div class="speed-line">
                <span class="text">{{ $t('dbgbase.speed') }}</span>
                <NewSelect class="operate-btn"></NewSelect>
            </div>
            <div class="drive-line">
                <span class="text">{{ $t('dbgbase.drive') }}</span>
                <NewSelect class="operate-btn"></NewSelect>
            </div>
            <div class="device-line">
                <span class="text">{{ $t('dbgbase.motor') }}</span>
                <NewSelect class="operate-btn"></NewSelect>
            </div>
        </div>
        <div class="horizontal-line"></div>

        <div class="base-box-three">
            <div class="diagnose-line">
                <span class="text text-underline">{{ $t('dbgbase.diagnosis') }}</span>
            </div>
            <IconTextSvgButton :module="module" class="diagnose-box" intervalSvgLeft="50%" intervalSvgTop="39%" intervalTextLeft="51%"
                intervalTextTop="72%" icon="diagnose" :text="$t('dbgbase.systemdiagnosis')" fontSize="1.375rem"
                fontWeight="bold" @click="dgns" width="17.5rem" height="11.5rem" iconSize="5.5rem">
            </IconTextSvgButton>
            <div class="up-reboot-box">
                <div class="updata-box">
                    <IconTextSvgButton :module="module" intervalSvgLeft="50%" intervalSvgTop="36%" intervalTextLeft="51%"
                        intervalTextTop="72%" icon="LowerUpd" :text="$t('dbgbase.update')" fontSize="1.375rem"
                        fontWeight="bold" @click="sysUpdate" width="8.5rem" height="11.5rem" iconSize="3.5rem">
                    </IconTextSvgButton>
                </div>
                <div class="restart-box">
                    <IconTextSvgButton :module="module" intervalSvgLeft="50%" intervalSvgTop="36%" intervalTextLeft="51%"
                        intervalTextTop="72%" icon="reboot" :text="$t('dbgbase.systemReboot')" fontSize="1.375rem"
                        fontWeight="bold" @click="reboot" width="8.5rem" height="11.5rem" iconSize="3.5rem">
                    </IconTextSvgButton>
                </div>
            </div>
            <div class="system-download-box">
                <LeetxButton :module="module" class="system-download-box3" :text="$t('dbgbase.configureInp')" width="8.5rem" height="11.5rem"
                    @click="importConfFile(1)" fontSize="1.375rem">
                </LeetxButton>
                <LeetxButton  :module="module" class="system-download-box4" :text="$t('dbgbase.psetInp')" width="8.5rem" height="11.5rem"
                    @click="importConfFile(2)" fontSize="1.375rem">
                </LeetxButton>
            </div>

            <div class="system-upload-box">
                <LeetxButton :module="module" class="system-upload-box3" :text="$t('dbgbase.configureOup')" width="8.5rem" height="11.5rem"
                    @click="exportConfFile(1)" fontSize="1.375rem">
                </LeetxButton>
                <LeetxButton :module="module" class="system-upload-box4" :text="$t('dbgbase.psetOup')" width="8.5rem" height="11.5rem"
                    @click="exportConfFile(2)" fontSize="1.375rem">
                </LeetxButton>
            </div>
        </div>
    </div>
</template>
<script>

import systemDgns from "../../dialogs/debug/systemdgns/systemDiagnosis"
import LeetxButton from "../controls/LeetxButton.vue";
import { devReboot, sysUpdate } from "@/api/device/device"
import cnfrmWin from "@/dialogs/common/confirm/cnfrm"
import NewSelect from '../select/NewSelect.vue'
import {
    motionAcrun, motionCease, motionRepeat, motionUnrepeat, deviceForward, deviceBackward, devJogStop, devJogInfo,
    devJogStart, pdataTightCnt
} from '../../api/motion.js'
import IconTextButton from '../controls/IconTextButton.vue';
import FormItem from '../form/FormItem.vue';
import InputVal from '../input/InputVal.vue';
import LeetxSwitch from '../switch/LeetxSwitch.vue';
import { messageApi } from '../message/message';
import { getsystemsync } from "@/api/websocketApi";
import IconTextSvgButton from '@/components/controls/IconTextSvgButton.vue'
import SvgSelect from '@/components/select/SvgSelect.vue'


export default {
    components: {
        LeetxButton,
        NewSelect,
        IconTextButton,
        FormItem,
        InputVal,
        LeetxSwitch,
        IconTextSvgButton,
        SvgSelect
    },
    data() {
        return {
            pdataTightCnt: 0,
            devParams: {
                // jog_speed: 0,
                // jog_maxtor: 0,
                // jog_drc: 1   //1:ÂèçËΩ¨.0Ê≠£ËΩ¨
            },
            udpateTimes: 0,
            module: "DEBUG",
            direction: [
                { label: this.$t('knowhat.frwrd'), key: 0, icon: 'Clockwise' },
                { label: this.$t('knowhat.bcwrd'), key: 1, icon: 'Contrarotate' },
            ]
        }
    },
    methods: {
        // ÂØºÂá∫Êñá‰ª∂
        exportConfFile(type) {
            // ‰ªéÊú¨Âú∞ÂØºÂá∫ÈÖçÁΩÆÊñá‰ª∂   
            let uploadCndtn = {
                uploadConf: true,
                type: type,
            }
            getsystemsync.send(JSON.stringify(uploadCndtn));
        },
        // ÂØºÂÖ•Êñá‰ª∂
        importConfFile(type) {
            // ‰ªéÊú¨Âú∞ÂØºÂÖ•ÈÖçÁΩÆÊñá‰ª∂
            let downloadCndtn = {
                downloadConf: true,
                type: type,
            }
            getsystemsync.send(JSON.stringify(downloadCndtn));

        },
        sysUpdate() {
            let title = this.$t('dbgbase.update')
            let confirmText = this.$t('tchnlgy.yes')
            let cancelText = this.$t('tchnlgy.no')
            let msg = this.$t('dbgbase.isUpdate')
            cnfrmWin({
                title,
                confirmText,
                cancelText,
                msg
            }).then(() => {
                sysUpdate();
            })
        },
        reboot() {
            let title = this.$t('dbgbase.reboot')
            let confirmText = this.$t('tchnlgy.yes')
            let cancelText = this.$t('tchnlgy.no')
            let msg = this.$t('dbgbase.softRestart')
            cnfrmWin({
                title,
                confirmText,
                cancelText,
                msg
            }).then(() => {
                devReboot();
            })
        },
        dgns() {
            systemDgns({ dgnsMethod: this.$t('systemdiagnosis.systemdiagnosis') })
        },
        // Â∑•Ëâ∫ÂêØÂä®
        run() {
            if (this.acRun) return
            motionAcrun().then((result) => {
                if (result.status != 0) {
                    messageApi.warning([`ÂêØÂä®Â§±Ë¥•`])
                }
            }).catch(() => {

            });
        },
        // Âæ™ÁéØËøêË°å
        runMore(status) {
            if (status == 1) {
                motionRepeat()
            } else {
                motionUnrepeat()
            }
        },
        // Â∑•Ëâ∫ÂÅúÊ≠¢
        stop() {
            if (!this.acRun) return
            motionCease().then((result) => {
                if (result.status != 0) {
                    messageApi.warning(['ÂêØÂä®Â§±Ë¥•'])
                }
            }).catch(() => {
                // messgaeApi.warning(['ÂêØÂä®Â§±Ë¥•'])
            });
        },
        backwardForver() {
            deviceBackward()
        },
        forward() {
            deviceForward()
        },
        getUnit(name) {
            return this.$store.state.sensors[name]
        },
        setParams(key, val) {
            this.devParams[key] = val
        },
        devStart() {
            let speed = this.devParams.jog_speed
            let maxtor = this.devParams.jog_maxtor
            let drc = this.devParams.jog_drc
            if (this.devStatus) return
            let params = { speed, maxtor, drc }
            devJogStart(params).then((result) => {
                if (result.status == 9094) {
                    messageApi.warning(['ÂêØÂä®Â§±Ë¥•'])
                }
            }).catch(() => {
                // messgaeApi.warning(['ÂêØÂä®Â§±Ë¥•'])
            });
        },
        devStop() {
            if (!this.devStatus) return
            devJogStop().then((result) => {
                if (result.status == 9094) {
                    messageApi.warning(['ÂÅúÊ≠¢Â§±Ë¥•'])
                }
            }).catch(() => {
                // messgaeApi.warning(['ÂÅúÊ≠¢Â§±Ë¥•'])
            });
        },
        getUseTimes() {
            pdataTightCnt().then((result) => {
                if (result.status == 0) {
                    this.pdataTightCnt = result.data.sdo_value
                }
            })
        },
        getDevParams() {
            devJogInfo().then((result) => {
                if (result.status == 0) {
                    this.devParams = result.data
                } else {
                    messageApi.warning(['Ëé∑ÂèñËÆæÂ§áËøêË°åÂèÇÊï∞Â§±Ë¥•'])
                }
            }).catch(() => {
                // messgaeApi.warning(['Ëé∑ÂèñËÆæÂ§áËøêË°åÂèÇÊï∞Â§±Ë¥•'])
            });
        },
        setUpdateTimes() {
            this.udpateTimes += 1
            return this.udpateTimes <= 4
        }
    },
    created() {
        this.getUseTimes()
        this.getDevParams()
    },
    computed: {
        devStatus() {
            return this.$store.state.g_status.jogstr
        },
        startOneIcon() {
            // ÁÇπÂä®ÂÅúÊ≠¢ Âπ∂‰∏î Ê≤°ÊúâÂ∑•Ëâ∫ÂêØÂä®
            if (!this.devStatus && !this.acRun) {
                return 'ActiveStartOne'
            }
            return 'StaticStartOne'
        },
        stopOneIcon() {
            // ÁÇπÂä®ÂÅúÊ≠¢ Âπ∂‰∏î Ê≤°ÊúâÂ∑•Ëâ∫ÂêØÂä®
            if (this.devStatus && !this.acRun) {
                return 'ActiveStopOne'
            }
            return 'StaticStopOne'
        },
        startOneTextColor() {
            if (!this.devStatus && !this.acRun) {
                return '#3b3b3b'
            }
            return '#969696'
        },
        stopOneTextColor() {
            if (this.devStatus && !this.acRun) {
                return '#3b3b3b'
            }
            return '#969696'
        },
        keeprun() {
            return this.$store.state.g_status.keeprun
        },
        startTwoIcon() {
            // Ê≤°ÊúâÂ∑•Ëâ∫ÂêØÂä® Âπ∂‰∏î ÁÇπÂä®Ê≤°ÊúâÂêØÁî®
            if (!this.acRun && !this.devStatus) {
                return 'ActiveStartTwo'
            }
            return 'StaticStartTwo'
        },
        stopTwoIcon() {
            if (this.acRun && !this.devStatus) {
                return 'ActiveStopTwo'
            }
            return 'StaticStopTwo'
        },
        startTwoTextColor() {
            if (!this.acRun && !this.devStatus) {
                return '#3b3b3b'
            }
            return '#969696'
        },
        stopTwoTextColor() {
            if (this.acRun && !this.devStatus) {
                return '#3b3b3b'
            }
            return '#969696'
        },
        acRun() {
            return this.$store.state.g_status.ac_run
        },
        allowSetParam() {
            // Ê≤°ÊúâÂ∑•Ëâ∫ÂêØÂä®Âπ∂‰∏îÁÇπÂä®ÂÅúÊ≠¢
            return !this.acRun && !this.devStatus
        },
        runOk() {
            return !this.$store.state.g_status["busy"] && this.$store.state.g_status["rok"];
        },
        enabled() {
            if (this.module in this.$store.state.f_status.permissions && this.$store.state.f_status.permissions[this.module] == 4)
                return true
            else
                return false
        },
        pointerEvent() {
            return this.enabled ? "auto" : "none";
        },
        opacity() {
            return this.enabled ? 1 : 0.8;
        },

    },
    watch: {
        runOk() {
            this.getDevParams()
        }
    },
    mounted() {
        getsystemsync.msghandler = (msg) => {
            let result = JSON.parse(msg);
            switch (result.code) {
                case 250:
                    messageApi.success([this.$t('download.success250', result.data), this.$t('download.downloading')])
                    break;
                case 252:
                    messageApi.success([this.$t('download.success252', result.data), this.$t('download.downloading')])
                    break;
                case 251:
                    messageApi.success([this.$t('download.success251', result.data), ""])
                    break;
                case 404:
                    messageApi.error([this.$t('download.error404'), ""])
                    break;
                case 405:
                    messageApi.error([this.$t('download.error405'), ""])
                    break;
                case 406:
                    messageApi.error([this.$t('download.error406'), this.$t('download.error406Two')])
                    break;
                default:
                    messageApi.success([result.desc])
                    break;
            }
        };
        if (getsystemsync.openhandler != null) {
            return true
        }
        getsystemsync.openhandler = () => {
            console.log("getsystemsync ws connected !!!");
        };
        getsystemsync.closehandler = () => {
            // defineIsInit = false;
            console.log("getsystemsync ws disconnected !!!");
        };
        // getsystemsync.close()
        getsystemsync.open()
    }
}
</script>
