<template>
    <div class="dgns-box" v-if="isShow">
        <div class="dgns-title-line">
            <span class="dgns-title-msg">{{ dgnsMethod }}</span>
            <LeetxButton class="chngpswrd-box-close" :useIcon="true" icon="Close" borderRadius="0" iconSize="1.2rem"
                bgcColor="transparent" width="3rem" height="3rem" @click="cancel"></LeetxButton>
        </div>
        <div class="dgns-err-msgs">
            <div v-for="(val, k) in devInfos" :key="k" class="dgns-err-msg">
                <span class="dgns-item">
                    {{ k }}
                </span>
                <span class="dgns-state">
                    {{ val }}
                </span>
            </div>
        </div>
        <br>
        <LeetxButton class="dgns-cnfrm-btn" :text="$t('systemdiagnosis.stopdiagnosis')" textColor="#fff"
            borderRadius="0.25rem" bgcColor="#00c155" width="30.3125rem" height="3.75rem" fontSize="2.25rem" @click="cancel">
        </LeetxButton>
</div>
</template>

<script>
import LeetxButton from '@/components/controls/LeetxButton.vue'

import { maskApi } from '../../mask/StackMask.vue'
import { devDiagnose } from "@/api/device/device"
export default {
    components: {
        LeetxButton
    },
    data() {
        return {
            isShow: true,
            devInfos: {},
            timer: undefined,
            maskId: 0,
        }
    },
    methods: {
        cancel() {
            this.isShow = false
            this.onCancel && this.onCancel(this.$t('dlgsys.stopDiagnosing'))
            // clearInterval(this.timer)
            maskApi.descend(this.maskId)
        },
        startSysDgns() {
            devDiagnose().then((x) => {
                this.devInfos = x.data
            })
            this.onConfirm && this.onConfirm(this.$t('dlgsys.startDiagnosis'))
        
        },
        initStatus() {
            this.maskId = maskApi.rise(15, this.cancel)
        }
    },
    props: {
        dgnsMethod: {
            type: String,
            default: () => { return this.$t('dlgsys.systemDiagnosis') }
        }
    },
    mounted() {
        this.initStatus()
        this.startSysDgns()
    },
}
</script>
