import axios_ins from "../utils/axios_ins";


export function motionCease(){
    return axios_ins({
        url:"motion/cease",
        method:'put'
    })
}
export function motionAcrun(){
    return axios_ins({
        url:"motion/acrun",
        method:'put'
    })
}
export function motionRepeat(){
    return axios_ins({
        url:"motion/repeat",
        method:'put'
    })
}
export function motionUnrepeat(){
    return axios_ins({
        url:"motion/unrepeat",
        method:'put'
    })
}

export function deviceBackward(){
    return axios_ins({
        url:'motion/jog/ccw',
        method:"put"
    })
}
export function deviceForward(){
    return axios_ins({
        url:'motion/jog/fwd',
        method:"put"
    })
}
export function devJogStart(params){
    return axios_ins({
        url:"dev/jog/start",
        method:"post",
        data:params
    })
}
