import { createApp } from "vue";
import langvue from "../../../plugins/langVue";
import SystemDiagnosis from "./SystemDiagnosis.vue"
// ÂØºÂá∫ÂáΩÊï∞ÂèØÈÄöËøáË∞ÉÁî® Confirm() ÂáΩÊï∞Âä®ÊÄÅÂàõÂª∫ ÂØπËØùÊ°Ü ÁªÑ‰ª∂
const systemDgns= (props) => {
    return new Promise((resolve, reject) => { //promiseÂ∞ÅË£ÖÔºåokÊâßË°åresolveÔºånoÊâßË°åreject
        // ÂàõÂª∫ÁªÑ‰ª∂ÂÆû‰æã
        let dom = createApp(SystemDiagnosis,props).use(langvue).mount(document.createElement("div"));
        // Ê∑ªÂä†ÂÖÉÁ¥†
        document.body.appendChild(dom.$el);
        // ÈáçÂÜôÁªÑ‰ª∂ÁöÑÂáΩÊï∞
        dom.onConfirm = (x) => {
            // ÁÇπÂáªÁ°ÆÂÆöË∞ÉÁî®
            resolve(x)
        }
        dom.onCancel = function (err) {
            // ÁÇπÂáªÂèñÊ∂àË∞ÉÁî®
            reject(err)
        }
    })
}
export default systemDgns