<template>
    <div :class="{ 'select': true, 'hight-light': isShow }">
        <svgloader v-if="showImages" class="input-svg" :icon="this.selectIcon" size="2rem"></svgloader>
        <input :id="sltId" class="select-val" type="text" readonly @click="showOptions" @blur="onBlur"
            v-model="selectValue" />
        <div class="btn" @click="showOptions">
            <svgloader icon="Vector" :class="['arrow', { 'rotate': rotate }]" :size="btnSize"></svgloader>
        </div>
        <div :class="{ 'mask-box': true, 'mask-box_bottom': bottom, 'isScroll': isScroll }" v-show="isShow">
            <ul :class="{ 'options-box': true }">
                <li class="option-text" v-for="option in options" :key="option.key" @mousedown="valChng(option)">
                    <svgloader v-if="showImages" class="select-svg" :icon="option.icon" size="2rem"></svgloader>
                    <span class="select-text">{{ option.label }}</span>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import svgloader from '@/components/icon_font/svgloader.vue'

export default {
    components: { svgloader },
    props: {
        //ÊòØÂê¶ÊòæÁ§∫svgÂõæ
        showImages: {
            type: Boolean,
            default: false
        },
        icon: {
            type: String,
            default: "Add"
        },
        iconSize: {
            type: String,
            default: "100% - 1rem"
        },
        bottom: {
            default: false
        },
        //ÊòØÂê¶ÊªöÂä®ÔºåtrueÔºö‰ª•maxSize‰∏∫ÂáÜÔºåfalseÔºöÂÖ®ÈÉ®Â±ïÁ§∫
        isScroll: {
            default: false
        },
        maxSize: {
            type: Number,
            default: 4,
        },
        options: {
            type: Object,
            default: () => {
                return {}
            }
        },
        // Â∞ÜËØ•propÂÄº‰Ωú‰∏∫selVÁöÑÂàùÂßãÂÄº
        realVal: {
            default: ""
        },
        color: {
            type: String,
            default: () => {
                return 'blue'
            }
        },
        isReturnKey: {
            type: Boolean,
            default: false
        },
        height: {
            type: Number,
            default: 3.125
        },
        width: {
            type: Number,
            default: 17.625
        },
        fntSize: {
            type: String,
            default: '1.625rem'
        },
        btnSize: {
            type: String,
            default: '1.2rem'
        },
        optional: {
            type: Boolean,
            default: true,
        },
        isAllowChng: {
            type: Boolean,
            default: true,
        },
        clbck: {
            type: Function,
            default: function () {
                return
            }
        },
        module: {
            type: String,
            default: "NULL",
        }
    },
    data() {
        return {
            selectValue: "",
            selectIcon: "",
            rotate: false,
            isShow: false,
            sltId: `slt-${Math.random().toFixed(6) * 1000000}`,
            zIndex: 33,
        }
    },
    methods: {
        showOptions() {
            if (!this.optional || !this.isAllowChng) {
                return
            }
            this.zIndex = this.zIndex == 33 ? 99 : 33
            this.isShow = !this.isShow
            this.rotate = !this.rotate
        },
        valChng(option) {
            // ‰∏çÂÖÅËÆ∏ÂàáÊç¢
            if (!this.isAllowChng) {
                this.clbck && this.clbck()
                return
            }
            this.selectValue = option.label
            this.selectIcon = option.icon
            this.onBlur()
            this.$emit('valChng', this.isReturnKey ? option.label : option.key)
            this.$emit('update:realVal', this.isReturnKey ? option.label : option.key)
        },
        onBlur() {
            this.isShow = false
            this.rotate = false
            this.zIndex = this.zIndex == 33 ? 99 : 33
        },
        getSize() {
            let minPct = Math.min(window.innerWidth / 800, window.innerHeight / 1280).toFixed(3)
            return minPct * 16
        },
        initData() {
            if (!this.isReturnKey) {
                 this.options.forEach(option => {
                    if (option.key === this.realVal) {
                        this.selectValue = option.label
                        this.selectIcon = option.icon
                    }
                 });
            } else {
                this.selectValue = this.realVal
            }

        },
    },
    computed: {
        optiponsHeight() {
            let allSize = Object.keys(this.options).length
            let size = allSize > this.maxSize ? this.maxSize : allSize
            let height = this.isScroll ? (size * (this.getSize() * this.height + 2)) / this.getSize()
                : this.height * allSize
            return height + 'rem'
        },
        optiponsWidth() {
            return ((this.width * this.getSize() + 18) / this.getSize() + 'rem')
        },
        maskWidth() {
            return (this.width + 'rem')
        },
        singleHeight() {
            return (this.height + 'rem')
        },
        innerWidth() {
            return ((this.width * this.getSize() - 4) / this.getSize()) + 'rem'
        },
        singleWidth() {
            return (this.width + 'rem')
        },
        textWidth() {
            return ((this.width * this.getSize() - 4) * 0.96 / this.getSize()) + 'rem'
        },
        // isScroll() {
        //     let allSize = Object.keys(this.options).length
        //     // return allSize > this.maxSize
        // },
        enabled() {
            if (this.isAllowChng == false)
                return false;
            if (this.module == "NULL")
                return true
            else if (this.module in this.$store.state.f_status.permissions && this.$store.state.f_status.permissions[this.module] == 4)
                return true
            else
                return false
        },
        pointerEvent() {
            return this.enabled ? "auto" : "none";
        },
        opacity() {
            return this.enabled ? 1 : 0.5;
        },

    },
    created() {
        this.initData()
    },
    watch: {
        options: {
            deep: true,
            immediate: true,
            handler: function () {
                this.initData()
            }
        },
        realVal: {
            deep: true,
            immediate: true,
            handler: function () {
                this.initData()
            }
        }
    },
}
</script>
<style scoped>
* {
    font-size: v-bind(fntSize) !important;
}

.select {
    box-sizing: border-box;
    width: v-bind(singleWidth) !important;
    line-height: v-bind(singleHeight) !important;
    border-radius: 4px;
    border: 1px solid var(--C65) !important;
    position: relative;
    pointer-events: v-bind(pointerEvent);
    opacity: v-bind(opacity);
}

.hight-light {
    border: 2px solid var(--C00c155) !important;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3);
}

.select-val {
    width: v-bind(innerWidth);
    height: calc(100% - 2px);
    line-height: v-bind(singleHeight) !important;
    text-align: center;
    cursor: pointer;
    border: none;
    outline: none;
    position: relative;
    z-index: 31;
    background-color: transparent;
    font-weight: 500;
    letter-spacing: 0px;
    color: var(--C65) !important;
    font-size: var(--font1_625);
    user-select: none;
    overflow: hidden;
}

.btn {
    position: absolute;
    top: 30%;
    transform: translateY(-50%);
    right: 1rem;
    z-index: 30;
}

.arrow {
    position: absolute;
    right: 0px;
    transition: transform 0.3s ease-in-out;
}

.rotate {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;
    transform: rotate(180deg);
}

.mask-box {
    position: absolute;
    top: calc(100% + 6px);
    left: -2px;
    width: v-bind(singleWidth) !important;
    height: v-bind(optiponsHeight) !important;
    overflow: hidden;
    box-sizing: border-box;
    /* border: 1px solid var(--C65); */
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3);
    z-index: 100;

}

.mask-box_bottom {
    top: unset;
    bottom: calc(100% + 6px);
}

.options-box {
    position: absolute;
    background: #FFFFFF;
    width: v-bind(optiponsWidth) !important;
    /* height: 100%; */
    top: 0;
    left: 0;
    color: #706F94;
    z-index: 2;
    border: 1px solid #dddddd;
    border-radius: 4px;
    padding-bottom: 6px;
    z-index: 100;

}

.option-text {
    display: flex;
    /* ÈíàÂØπÂ≠êÂÖÉÁ¥†ÁöÑÂûÇÁõ¥ÊñπÂêëÂØπÈΩêÊñπÂºè */
    align-items: center;
    /* ÂØπÂ≠êÂÖÉÁ¥†ÁöÑÊ∞¥Âπ≥ÊñπÂêëÂØπÈΩêÊñπÂºè */
    justify-content: center;
    width: v-bind(textWidth) !important;
    height: v-bind(singleHeight);
    line-height: v-bind(singleHeight);
    cursor: pointer;
    color: var(--C65);
    font-size: var(--font1_625);
    border-radius: 2px;
    text-align: center;
    font-weight: 500;
    letter-spacing: 0px;
    color: var(--C65) !important;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.option-text:active {
    color: var(--C00);
}

.isScroll {
    overflow-y: scroll;
}

.isScroll::-webkit-scrollbar {
    width: 6px;
    height: 5px;
    background-color: transparent;
}

.isScroll::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #dfe3e6;
}

.input-svg {
    position: absolute;
    top: 20%;
    left: 14%;
    width: 3vw;
    float: left;
}

.select-svg {
    width: 3vw;
    float: left;
    margin-left: -2vw;
    margin-right: 4vw;
}

.select-text {
    width: 3vw;
    display: inline-block;
}
</style>